          "styles.css": ".tabbar {\n  list-style-type: none;\n  display: flex;\n  align-items: flex-start;\n  padding: 0;\n  margin-bottom: 0;\n}\n\n.tabbar li {\n  padding: 10px;\n  cursor: pointer;\n}\n\n.tabbar li.active {\n  background-color: #f8f8f8;\n}\n\n.tabcontainer {\n\n}\n\n.tabcontainer pre {\n  margin-top: 0;\n}\n",
          "template.hbs": "<div>\n  <ul local-class=\"tabbar\">\n    {{#each this.pages as |page|}}\n      <li local-class={{if (eq this.activePage page) 'active'}} {{on \"click\" (bind this.activate page)}}>{{page.title}}</li>\n    {{/each}}\n  </ul>\n\n  <div local-class=\"tabcontainer\">\n    {{yield (hash\n      Page=(component 'page' parent=this)\n    )}}\n  </div>\n</div>"
